import { Meta } from '@storybook/react';
import styled from 'styled-components';
import TemplateBuilder from '../TemplateBuilder';
import { templateBuilderToHTML, download, templateBuilderStore } from '../..';
import { convertBuilderData } from './utils/importBuilderData';
import subotData from './utilsStory/subot';

export default {
  title: 'TemplateBuilder',
  argTypes: {},
} as Meta;

const StyledPreviewIframe = styled.div`
  position: relative;

  iframe {
    margin: 0 auto;
    display: block;
    position: relative;
    z-index: 1;
    top: 0;
    left: 0px;
  }
  svg {
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, 0);
  }

  &.mobile-view {
    padding-top: 60px;
    iframe {
      border: 1px solid #c3c3c3;
    }
  }
`;

async function fileToJSON(file: any) {
  return new Promise((resolve, reject) => {
    const fileReader = new FileReader();
    fileReader.onload = (event: any) => {
      try {
        resolve(JSON.parse(event.target.result));
      } catch (err) {
        reject(err);
      }
    };
    fileReader.onerror = (error) => reject(error);
    fileReader.readAsText(file);
  });
}

type Props = {
  supportType: 'email' | 'landing-page';
};

const Default = ({ supportType }: Props) => {
  const ref = useRef<{ getData: () => any; setData: (data: any) => void }>({
    getData: () => {},
    setData: (data: any) => {},
  });
  const [isShow, setIsShow] = useState(false);
  const [htmlTemplate, setHtmlTemplate] = useState('');
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    templateBuilderStore.setFileHandler((resolve, reject) => {
      // alert('Handle upload from external app');
      const inputFile = document.createElement('input');
      inputFile.type = 'file';
      inputFile.accept = 'image/png, image/gif, image/jpeg';
      inputFile.addEventListener('change', handleFileUpload);

      function handleFileUpload(event: Event): void {
        const target = event.target as HTMLInputElement;
        const file: File | null = (target.files && target.files[0]) || null;

        // Only continue if a file was selected and it is an image
        if (file && file.type.startsWith('image/')) {
          resolve({
            url: URL.createObjectURL(file),
            context: '',
          });

          // const reader = new FileReader();
          // reader.readAsDataURL(file);

          // // When the reader has finished reading the file, convert it to base64
          // reader.onload = () => {
          //   const base64 = reader.result as string;
          //   resolve({
          //     url: base64,
          //     context: '',
          //   });
          // };
        }
      }

      inputFile.click();
    });
    templateBuilderStore.templateLocale = 'id-ID';
  }, []);

  useMemo(() => {
    templateBuilderStore.setFetchSubot(() => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(subotData.data.bots);
        }, 500);
      });
    });
  }, []);

  useEffect(() => {
    templateBuilderStore.setFetchArticle(() => {
      return new Promise((resolve) => {
        setTimeout(() => {
          // resolve(null);
          resolve({
            url: 'https://discover.hellobacsi.com/suc-khoe/dau-ha-suon-trai-la-benh-gi/',
            title: 'Đau hạ sườn trái là bệnh gì? Không chỉ là vấn đề tiêu hóa',
            thumbnail: 'https://cdn.hellobacsi.com/wp-content/uploads/2022/08/dau_ha_suon_trai-150x150.jpg',
            description:
              'Đau hạ sườn trái là cơn đau chủ yếu ở góc nửa phía dưới bên trái của bụng. Khu vực vùng bụng bên trái có thể mềm khi chạm vào kèm theo cơn đau dữ dội và khiến toàn bộ vùng bụng bị căng cứng. Nguyên nhân gây đau chủ yếu liên quan đến đường tiêu hóa, nhưng cũng có thể liên quan đến các cơ quan khác, chẳng hạn như da, mạch máu, đường tiết niệu hoặc cơ quan sinh sản ở phụ nữ và nam giới.',
            authorName: 'Trúc Phạm',
            authorAvatar: 'https://cdn.hellobacsi.com/wp-content/uploads/2021/04/64tg-scaled-150x150.jpg',
          });
        }, 500);
      });
    });
  }, []);

  const handleClickImport = () => {
    const inputEl = document.createElement('input');
    inputEl.type = 'file';

    inputEl.addEventListener('change', (evt: any) => {
      const file = evt.target?.files?.[0];

      if (file) {
        fileToJSON(file)
          .then((res: any) => {
            const newBuilderData = convertBuilderData(res.page);

            if (newBuilderData) {
              ref.current.setData(newBuilderData);
              message.info('Import successfully!');
            }
          })
          .catch((err) => {
            console.log('err', err);
            message.error('Something went wrong. Please try again!');
          });
      }
    });

    inputEl.click();
  };

  return (
    <div>
      {supportType === 'email' && (
        <div style={{ paddingLeft: '20px' }}>
          <Button
            type="primary"
            onClick={() => {
              const builderData = ref.current.getData();
              const srcDoc = templateBuilderToHTML(builderData);
              setHtmlTemplate(srcDoc);
              setIsShow(true);
            }}
          >
            Preview
          </Button>
          <Button type="link" onClick={handleClickImport}>
            Import
          </Button>
        </div>
      )}
      <div style={{ height: '90vh', margin: 2 }}>
        <TemplateBuilder
          localStorageKey={supportType === 'email' ? 'builderData' : 'landingData'}
          templateType={supportType}
          leadCampaigns={[]}
          ref={ref}
        />
      </div>
      <Drawer
        title="Preview" //
        placement="right"
        width="80vw"
        onClose={() => setIsShow(false)}
        visible={isShow}
        extra={
          <Space>
            <Button
              type={isMobile ? 'primary' : undefined} //
              icon={<MobileOutlined />}
              onClick={() => setIsMobile(!isMobile)}
            >
              Mobile
            </Button>
            <Button
              onClick={() => {
                download(`EmailTemplate_untitled_${Date.now()}.html`, htmlTemplate);
              }}
              icon={<ArrowDownOutlined />}
            ></Button>
          </Space>
        }
      >
        {isShow && htmlTemplate && (
          <StyledPreviewIframe className={isMobile ? 'mobile-view' : ''}>
            {isMobile && <MobileBackground />}
            <iframe
              title="Preview" //
              style={{
                width: isMobile ? '320px' : '100%', //
                height: isMobile ? '480px' : 'calc(100vh - 120px)',
              }}
              frameBorder="0"
              srcDoc={htmlTemplate.replace(
                '<style>',
                `<style>
                  body {
                    overflow: overlay;
                  }
                  body::-webkit-scrollbar {
                    width: 6px;
                    height: 6px;
                  }
                  
                  body::-webkit-scrollbar-track {
                    background-color: transparent;
                  }
                  
                  body::-webkit-scrollbar-thumb {
                    border-radius: 6px;
                    background-color: rgba(0, 0, 0, 0.4);
                  }`,
              )}
            />
          </StyledPreviewIframe>
        )}
      </Drawer>
    </div>
  );
};

export const Email = () => {
  return <Default supportType="email" />;
};

export const LandingPage = () => {
  return <Default supportType="landing-page" />;
};

