import React, { useState } from 'react';
import { Input } from 'antd';
import { WidgetProps } from '../index';
import { WidgetWrap } from '../index.styled';
import styled from 'styled-components';
import { getDataEventAction } from '../../../TemplateBuilder/utils/tracking';

const StyledWidgetWrap = styled(WidgetWrap)`
  input {
    margin-top: 5px;
    font-size: 12px;
  }
`;

function WidgetTrackingUUID({ value: valueProp, initValue, onChange, name }: WidgetProps<string>) {
  const [uuid] = useState(initValue || valueProp || '');

  return (
    <StyledWidgetWrap>
      <label>Data Event Category</label>
      <Input value={getDataEventAction(name, uuid)} name="dataEventCategory" disabled />
    </StyledWidgetWrap>
  );
}

export { WidgetTrackingUUID };
