import React from 'react';
import { WidgetPath, WidgetProps } from '..';
import { Collapse } from 'antd';
import styled from 'styled-components';
import { WidgetFormCampaignCode, WidgetFormText } from '../widgetFormCampaignCode';

type WidgetValueProp = Record<string, string>;

const { Panel } = Collapse;

const StyledCollapse = styled(Collapse)`
  font-size: 12px;
  margin: 15px 20px;
  background-color: #eaeaea;

  .ant-input-group .ant-input {
    font-size: 12px;
  }

  & > .ant-collapse-item > .ant-collapse-header {
    padding: 12px 16px 10px 16px;
    font-weight: 700;
    text-transform: uppercase;
    color: #505659;
  }
  .ant-collapse-content > .ant-collapse-content-box {
    padding-left: 0;
    padding-right: 0;
    padding-top: 0;
    padding-bottom: 0px;

    > div:last-child {
      border-bottom: 0;
    }
  }
`;

function WidgetFormManageOpts({ value, initValue, onChange, name: widgetName }: WidgetProps<WidgetValueProp>) {
  const handleChange = (_: string, singleVal: string | Record<string, string>, name?: string) => {
    if (!name || !value) {
      return;
    }

    if (typeof singleVal === 'string') {
      onChange(WidgetPath.FormManageOptions, { ...value, [name]: singleVal }, widgetName);
    } else if (typeof singleVal === 'object') {
      onChange(WidgetPath.FormManageOptions, { ...value, ...singleVal }, widgetName);
    }
  };

  return (
    <StyledCollapse>
      <Panel header="Form Options" key="formOptions">
        <WidgetFormCampaignCode
          value={value?.['campaign_code'] || ''} //
          name="campaign_code"
          onChange={handleChange}
        />
        <WidgetFormText
          value={value?.['message_success'] || ''} //
          name="message_success"
          onChange={handleChange}
          path=""
          label="Message success"
        />
        <WidgetFormText
          value={value?.['message_error'] || ''} //
          name="message_error"
          onChange={handleChange}
          path=""
          label="Message error"
        />
        <WidgetFormText
          value={value?.['thank_you_url'] || ''} //
          name="thank_you_url"
          onChange={handleChange}
          path=""
          label="Thanks Page"
        />
      </Panel>
    </StyledCollapse>
  );
}

export { WidgetFormManageOpts };
