import React, { useEffect, useRef, useState } from 'react';
import { WidgetPath, WidgetProps } from '../index';
import { WidgetWrap } from '../index.styled';
import styled from 'styled-components';
import { Input, Row, Col } from 'antd';
import { countdown } from '../../../TemplateBuilder/modules/defaultJson';

type TypeWordingValue = {
  day: string;
  hour: string;
  minute: string;
  second: string;
};

const StyledRow = styled(Row)`
  margin-top: 15px;
  padding-top: 15px;
  border-top: 1px solid #ddd;

  p {
    font-size: 12px;
    margin-bottom: 2px;
    color: #8f9699;
  }

  .ant-input {
    font-size: 12px;
  }
`;

const defaultWording = countdown().descriptor.countdown.wording;

function WidgetCountdownWording({ value: valueProp, initValue, onChange, name }: WidgetProps<TypeWordingValue>) {
  const [wording, setWording] = useState(initValue || valueProp || defaultWording);
  const timeoutId = useRef<NodeJS.Timeout | undefined>();

  const handleChange = (evt: any) => {
    const newWording = {
      ...wording,
      [evt.target.name]: evt.target.value,
    };
    setWording(newWording);

    if (timeoutId.current) {
      clearTimeout(timeoutId.current);
      timeoutId.current = undefined;
    }

    timeoutId.current = setTimeout(() => {
      onChange(WidgetPath.CountdownWording, newWording, '');
    }, 300);
  };

  useEffect(() => {
    return () => {
      timeoutId.current && clearTimeout(timeoutId.current);
    };
  }, []);

  return (
    <WidgetWrap>
      <label>Countdown Content</label>
      <StyledRow gutter={[15, 5]}>
        <Col xs={12}>
          <p>Day</p>
          <Input size="small" value={wording.day} onChange={handleChange} name="day" />
        </Col>
        <Col xs={12}>
          <p>Hour</p>
          <Input size="small" value={wording.hour} onChange={handleChange} name="hour" />
        </Col>
        <Col xs={12}>
          <p>Minute</p>
          <Input size="small" value={wording.minute} onChange={handleChange} name="minute" />
        </Col>
        <Col xs={12}>
          <p>Second</p>
          <Input size="small" value={wording.second} onChange={handleChange} name="second" />
        </Col>
      </StyledRow>
    </WidgetWrap>
  );
}

export { WidgetCountdownWording };
