import React, { useEffect, useState } from 'react';
import { Input } from 'antd';
import { WidgetPath, WidgetProps } from '../index';
import { WidgetWrap } from '../index.styled';
import styled from 'styled-components';

const StyledWidgetWrap = styled(WidgetWrap)`
  .ant-input-group-addon {
    font-size: 12px;
  }
`;

type WidgetFormTextProps = WidgetProps<string> & {
  label: string;
  path: string;
};

export function WidgetFormText({ label, path, value: valueProp, initValue, onChange, name }: WidgetFormTextProps) {
  const [value, setValue] = useState(initValue || valueProp || '');

  useEffect(() => {
    if (valueProp) {
      setValue(valueProp);
    }
  }, [valueProp]);

  function handleChange(evt: any) {
    setValue(evt.target.value);
  }

  function handleBlur() {
    onChange(path, value, name);
  }

  return (
    <StyledWidgetWrap>
      <label></label>
      <Input
        addonBefore={label} //
        onBlur={handleBlur}
        max={256}
        value={value}
        name={name}
        onChange={handleChange}
        size="small"
      />
    </StyledWidgetWrap>
  );
}

function WidgetFormCampaignCode(props: WidgetProps<string>) {
  return <WidgetFormText {...props} label="Campaign Code" path={WidgetPath.FormCampaignCode} />;
}

export { WidgetFormCampaignCode };
