import React, { useRef, useState } from 'react';
import { WidgetPath, WidgetProps } from '..';
import { WidgetWrap } from '../index.styled';
import styled from 'styled-components';
import { WidgetSectionTitle } from '../../../atoms/widgetSectionTitle';
import { Row, Input } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import templateBuilderStore from '../../../TemplateBuilder/store';

const StyledWrap = styled.div`
  .ant-row {
    width: 100%;

    & + .ant-row {
      margin-top: 8px;
    }
  }

  label {
    color: #8f9699;
    font-size: 12px;
    margin-bottom: 2px;
  }

  .ant-input {
    font-size: 12px;
  }
`;

type WidgetValueProp = Record<string, string>;

function WidgetArticleContent({ value, onChange }: WidgetProps<WidgetValueProp>) {
  const timeoutRef = useRef<NodeJS.Timeout>();
  const [loading, setLoading] = useState(false);
  const [url, setUrl] = useState(value?.url || '');

  function handleChangeUrl(evt: any) {
    setUrl(evt.target.value);

    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current);
      timeoutRef.current = undefined;
    }

    timeoutRef.current = setTimeout(() => {
      setLoading(true);
      templateBuilderStore.articleFetching?.(evt.target.value).then((data) => {
        if (data) {
          onChange(WidgetPath.ArticleContent, data);
        }
        setLoading(false);
      });
    }, 300);
  }

  if (!value) {
    return null;
  }

  return (
    <StyledWrap>
      <WidgetSectionTitle title="Article Content" />
      <WidgetWrap>
        <Row>
          <label>Article URL</label>
          <Input
            placeholder="https://..."
            size="small"
            value={url}
            onChange={handleChangeUrl}
            suffix={loading ? <LoadingOutlined /> : undefined}
          />
        </Row>
        <Row>
          <label>Title</label>
          <Input size="small" disabled={true} value={value.title} />
        </Row>
        <Row>
          <label>Description</label>
          <Input.TextArea size="small" disabled={true} value={value.description} rows={4} />
        </Row>
        <Row>
          <label>Thumbnail</label>
          <Input size="small" disabled={true} value={value.thumbnail} />
        </Row>
        <Row>
          <label>Author Name</label>
          <Input size="small" disabled={true} value={value.authorName} />
        </Row>
        <Row>
          <label>Author Avatar</label>
          <Input size="small" disabled={true} value={value.authorAvatar} />
        </Row>
      </WidgetWrap>
    </StyledWrap>
  );
}

export { WidgetArticleContent };
