import { Row, Switch, InputNumber, Col } from 'antd';
import React, { useEffect, useState } from 'react';
import { WidgetPath, WidgetProps } from '../index';
import { StyledWidgetWrap, StyledWidgetRadiusOpts, StyledRadiusPreview } from './index.styled';

export type TypeRadiusValue = {
  'border-top-left-radius': string;
  'border-bottom-right-radius': string;
  'border-top-right-radius': string;
  'border-bottom-left-radius': string;
  'border-radius': string; // To reset
};

type WidgetRadiusValue = WidgetProps<TypeRadiusValue> & {
  label: string;
  path: string;
};

function isEqualRadiusOpts(value?: TypeRadiusValue, comparedValue?: TypeRadiusValue) {
  if (!value || !comparedValue) {
    return false;
  }

  return (
    value['border-top-left-radius'] === comparedValue['border-top-left-radius'] &&
    value['border-bottom-right-radius'] === comparedValue['border-bottom-right-radius'] &&
    value['border-top-right-radius'] === comparedValue['border-top-right-radius'] &&
    value['border-bottom-left-radius'] === comparedValue['border-bottom-left-radius']
  );
}

function getIsShowMoreOpt(value?: TypeRadiusValue) {
  if (!value) {
    return false;
  }

  const top = value['border-top-left-radius'];
  const right = value['border-bottom-right-radius'];
  const bottom = value['border-top-right-radius'];
  const left = value['border-bottom-left-radius'];

  return !(top === right && top === bottom && top === left);
}

export function WidgetStyleRadius({ label, path, value: valueProp, initValue, onChange, name }: WidgetRadiusValue) {
  const [value, setValue] = useState<TypeRadiusValue>(() => {
    return (
      initValue ||
      valueProp || {
        'border-top-left-radius': '0px',
        'border-bottom-right-radius': '0px',
        'border-top-right-radius': '0px',
        'border-bottom-left-radius': '0px',
        'border-radius': '',
      }
    );
  });
  const [isShowMoreOpts, setIsShowMoreOpts] = useState(() => {
    return getIsShowMoreOpt(initValue || valueProp);
  });

  useEffect(() => {
    if (valueProp) {
      setValue((v) => {
        if (isEqualRadiusOpts(v, valueProp)) {
          return v;
        }

        setIsShowMoreOpts(getIsShowMoreOpt(valueProp));

        return valueProp;
      });
    }
  }, [valueProp]);

  const handleChangeChecked = (newChecked: boolean) => {
    setIsShowMoreOpts(newChecked);
    if (!newChecked) {
      const newValue = {
        'border-top-left-radius': value['border-top-left-radius'],
        'border-bottom-right-radius': value['border-top-left-radius'],
        'border-top-right-radius': value['border-top-left-radius'],
        'border-bottom-left-radius': value['border-top-left-radius'],
        'border-radius': '',
      };
      setValue(newValue);
      onChange(path, newValue, name);
    }
  };

  const onChangeAllSide = (num: number | null) => {
    if (num === null) {
      return;
    }

    const newValue = {
      'border-top-left-radius': `${num}px`,
      'border-bottom-right-radius': `${num}px`,
      'border-top-right-radius': `${num}px`,
      'border-bottom-left-radius': `${num}px`,
      'border-radius': '',
    };
    setValue(newValue);
    onChange(path, newValue, name);
  };

  const onChangeOneSide = (key: keyof TypeRadiusValue, num: number | null) => {
    if (num === null) {
      return;
    }

    const newValue = {
      'border-top-left-radius': value['border-top-left-radius'],
      'border-bottom-right-radius': value['border-bottom-right-radius'],
      'border-top-right-radius': value['border-top-right-radius'],
      'border-bottom-left-radius': value['border-bottom-left-radius'],
      'border-radius': '',
      [key]: `${num}px`,
    };
    setValue(newValue);
    onChange(path, newValue, name);
  };

  return (
    <StyledWidgetWrap>
      <label>{label}</label>
      <Row align="middle">
        <span className="more-opts">More Options</span>&nbsp;&nbsp;
        <Switch size="small" checked={isShowMoreOpts} onChange={handleChangeChecked} />
      </Row>
      <StyledWidgetRadiusOpts>
        <Row justify="space-between" align="top">
          {!isShowMoreOpts ? (
            <div>
              <p>All sides</p>
              <InputNumber
                min={0} //
                max={200}
                value={parseInt(value['border-top-left-radius']) || 0}
                onChange={onChangeAllSide}
                size="small"
              />
            </div>
          ) : (
            <Row style={{ flex: 1 }}>
              <Col span={10}>
                <p>Top Left</p>
                <InputNumber
                  min={0} //
                  max={200}
                  value={parseInt(value['border-top-left-radius']) || 0}
                  onChange={(num) => onChangeOneSide('border-top-left-radius', num)}
                  size="small"
                />
                <p>Top Right</p>
                <InputNumber
                  min={0} //
                  max={200}
                  value={parseInt(value['border-top-right-radius']) || 0}
                  onChange={(num) => onChangeOneSide('border-top-right-radius', num)}
                  size="small"
                />
              </Col>
              <Col span={10}>
                <p>Bottom Left</p>
                <InputNumber
                  min={0} //
                  max={200}
                  value={parseInt(value['border-bottom-left-radius']) || 0}
                  onChange={(num) => onChangeOneSide('border-bottom-left-radius', num)}
                  size="small"
                />
                <p>Bottom Right</p>
                <InputNumber
                  min={0} //
                  value={parseInt(value['border-bottom-right-radius']) || 0}
                  onChange={(num) => onChangeOneSide('border-bottom-right-radius', num)}
                  size="small"
                />
              </Col>
            </Row>
          )}
          <StyledRadiusPreview $value={value} />
        </Row>
      </StyledWidgetRadiusOpts>
    </StyledWidgetWrap>
  );
}

function WidgetBorderRadiusOpts(props: WidgetProps<TypeRadiusValue>) {
  return <WidgetStyleRadius {...props} label="Border Radius" path={WidgetPath.BorderRadiusOpts} />;
}

export { WidgetBorderRadiusOpts };
