const StyledList = styled.ul`
  margin: 0;
  padding: 0;
  list-style-type: none;

  li {
    cursor: pointer;
    padding: 5px 10px;
    text-align: center;

    &:hover {
      background-color: rgba(0, 0, 0, 0.1);
    }
  }
`;

type Props = {
  onClick: (key: string) => void;
};

function ToolbarFontSize({ onClick }: Props) {
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [isModalVisibleDelay, setIsModalVisibleDelay] = useState(false);

  useEffect(() => {
    if (!isModalVisible) {
      setTimeout(() => {
        setIsModalVisibleDelay(isModalVisible);
      }, 300);
    }
  }, [isModalVisible]);

  return (
    <>
      <button
        className="toolbar-variables"
        onClick={() => {
          setIsModalVisible(true);
          setIsModalVisibleDelay(true);
        }}
      >
        <span>Vars</span>
      </button>
      {isModalVisibleDelay && (
        <Modal
          visible={isModalVisible}
          onCancel={() => setIsModalVisible(false)}
          okText={null}
          cancelText="Cancel"
          footer={null}
          width={300}
          title="Variable Tags"
        >
          <StyledList>
            {templateBuilderStore.listVariables.map((v) => {
              return (
                <li
                  title={v.key}
                  key={v.key}
                  onClick={() => {
                    onClick(v.key);
                    setIsModalVisible(false);
                  }}
                >
                  {v.label}
                </li>
              );
            })}
          </StyledList>
        </Modal>
      )}
    </>
  );
}

export default ToolbarFontSize;
